import React, { Component } from "react";
import Carousel from "react-spring-3d-carousel";
import {v1 as uuidv4} from "uuid";
import TniCard from "./card";
import { config } from "react-spring";
import image from "../../assets/Images/why us.jpg";


class SlideCarousel extends Component {
  constructor(props) {
    super(props);
    this.state = {
      goToSlide: ""
    };
  }
  slides = [
    {
        key: uuidv4(),
      content: <TniCard name="About Us" n1="" n2="" url="/#/about"/>
    },
    {
      key: uuidv4(),
      content: <TniCard name="Technology Stack" n1="React" n2="Angular" url="/#"/>
    },
    {
      key: uuidv4(),
      content: <TniCard name="Services" n1="" n2="" url="/#"/>
    },
    {
      key: uuidv4(),
      content: <TniCard name="Fintech" n1="" n2="" url="/fintech"/>
    },
    {
      key: uuidv4(),
      content: <TniCard name="Blog" n1="" n2="" url="/blog"/>
    },
  ].map((slide, index) => {
    return { ...slide, onClick: () => this.setState({ goToSlide: index }) };
  });

  

  render() {
    return (
      <div style={{ width: "50%", height: "240px", margin: "0 auto" }}>
        <Carousel
          slides={this.slides}
          goToSlide={this.state.goToSlide}
          offsetRadius="10"
          animationConfig={config.gentle}
        />
      </div>
    );
  }
}
export default SlideCarousel;