import React from "react";
import tw from "twin.macro";
import styled from "styled-components";
import { NavLink } from "react-router-dom";

export default class TniCard extends React.Component {
  constructor(props) {
    super(props);
  }

  

  static get CARD_STYLE() {
    return {
      height: "200px",
      width: "200px",
      paddingTop: "65px",
      textAlign: "center",
      background: "#2f429e",
      color: "#FFF",
      fontFamily: "sans-serif",
      fontSize: "12px",
      textTransform: "uppercase",
      borderRadius: "10px",
      boxSizing: "border-box",
    //   margin:"-20px",
    };
  }

  render = () => {
    const NavLinks = tw.div`inline-block`;
    const NavLink = tw.a`
        text-lg my-2 lg:text-sm lg:mx-6 lg:my-0
        font-semibold tracking-wide transition duration-300
        pb-1 border-b-2 border-transparent hover:border-gray-900 hocus:text-gray-900 text-gray-200
        `;

const LogoLink = styled(NavLink)`
  ${tw`flex items-center font-black border-b-0 text-2xl! ml-5`};
  img {
    ${tw`w-40  mr-3`}
  }
`;
    return (
      <NavLinks key={1}>
        <div style={TniCard.CARD_STYLE}>
        <NavLink href={`${this.props.url}`}><h2 style={{fontSize:"15px", paddingBottom:"20px"}}>{this.props.name}</h2></NavLink>
        <div className="row">
            <div className="col-md-6">
                {this.props.icon?
            <LogoLink href="/">
                <img src={this.props.icon} alt="logo" />
            </LogoLink> : ""}
            </div>
            <div className="col-md-6">
            
            </div>
        </div>
        </div>
      </NavLinks>
    );
  };
}